.about {
	padding-top: 51px;
	padding-bottom: 51px;
	background: conic-gradient(
		from 130.31deg at 16.03% 39.06%,
		#b80027 -46.69deg,
		#003d85 32.24deg,
		#b80027 313.31deg,
		#003d85 392.24deg
	);
	color: $color-text-light;

	@include vp-tablet {
		padding-bottom: 82px;
	}

	@include vp-mobile {
		padding-top: 51px;
		padding-bottom: 79px;
	}

	h1 {
		order: 1;
		margin: 0;
		font-family: $ff-alumni;
		font-weight: 600;
		font-size: 92px;
		line-height: 110px;
		text-align: right;
		letter-spacing: 0.05em;
		margin-top: 15px;
		text-transform: uppercase;
		-webkit-text-stroke-width: 2px;
		-webkit-text-stroke-color: $color-text-light;

		@include vp-tablet {
			font-size: 72px;
			line-height: 86px;
			text-transform: none;
			margin-top: 8px;
			max-width: 280px;
		}

		@include vp-mobile {
			margin: 0;
			flex: 0 0 50%;
			max-width: fit-content;
			margin-left: -19px;
			padding-left: 110px;
		}
	}

	p {
		font-family: $ff-alumni;
		font-weight: 600;
		font-size: 48px;
		line-height: 58px;
		letter-spacing: 0.03em;
		text-align: right;
		margin: 0;
		margin-right: 50px;
		grid-column: 2/3;
		grid-row: 1/2;
		order: 0;
		text-transform: lowercase;

		@include vp-tablet {
			font-size: 34px;
			line-height: 41px;
			margin-right: 4px;
		}

		@include vp-mobile {
			font-size: 28px;
			line-height: 34px;
			margin-right: 4px;
			margin-bottom: 14px;
		}
	}
}

.about__container {
	display: flex;
	width: 100%;
}

.about__content {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	position: relative;
	padding-top: 111px;
	padding-left: 149px;
	margin-right: 65px;

	@include vp-tablet {
		padding-top: 79px;
		width: auto;
		margin-right: 28px;
		padding-left: 100px;
	}

	@include vp-mobile {
		padding-left: 0;
		padding-top: 57px;
		width: 100%;
		margin-right: 0;
	}

	br {
		display: none;

		@include vp-mobile {
			display: block;
		}
	}
}

.about__button {
	order: 2;
	width: 95%;
	margin-top: 48px;
	margin-left: auto;
	flex-shrink: 0;

	@include vp-tablet {
		width: 83%;
		margin-top: 37px;
		margin-left: auto;
	}

	@include vp-mobile {
		padding: 20px;
		padding-top: 22px;
		padding-bottom: 18px;
		width: 100%;
		justify-content: center;
		margin-top: 22px;
	}
}

.about__list {
	list-style: none;
	margin: 0;
	padding: 0;
	width: max-content;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	position: absolute;
	top: 305px;
	left: 19px;

	@include vp-tablet {
		text-align: right;
		align-self: flex-end;
		left: 0;
		top: 217px;
		padding: 0;
	}

	@include vp-mobile {
		padding-bottom: 30px;
		padding-top: 70px;
		top: 110px;
		left: 0;
	}

	li {
		font-family: $ff-roboto;
		font-weight: 400;
		font-size: 14px;
		line-height: 1.5;
		width: max-content;
	}
}

.about__image {
	flex-shrink: 0;
	max-width: 500px;
	height: auto;
	margin-left: auto;
	margin-right: 199px;

	@include vp-tablet {
		max-width: 324px;
		margin: 0;
		height: auto;
		margin-top: 31px;
		margin-left: auto;
	}

	@include vp-mobile {
		display: none;
	}

	img {
		width: 100%;
		height: auto;
		object-fit: contain;

		@include vp-tablet {
			aspect-ratio: 324/390;
			object-fit: cover;
		}
	}
}
